:root {
	--theme: #15a884;
}

#castells {
	margin-top: 20px;
}

#castells .castell {
	float: left;
	height: 50px;
	width: calc(25% - 20px);
	text-align: center;
	margin: 10px;
}

@media (max-width: 250px) {
	#castells .castell {
		width: calc(100% - 20px);
	}
}
@media (min-width: 250px) and (max-width: 350px) {
	#castells .castell {
		width: calc(50% - 20px);
	}
}
@media (min-width: 350px) and (max-width: 450px) {
	#castells .castell {
		width: calc(100%/3 - 20px);
	}
}
@media (min-width: 1000px) {
	#castells .castell {
		width: calc(12.5% - 20px);
	}
}

html, body {
	margin: 0;
	background-color: white;
	font-family: 'Arial';
}

h1 {
	color: var(--theme);
	font-size: 2.5em;
	text-align: center;
	margin: 0 10%;
}

#castells .wrap {
	display: flow-root;
	margin: 50px 5%;
}

.castell h2 {
	color: var(--theme);
	font-size: 1.5em;
	margin: 0;
}

.castell h3 {
	color: gray;
	font-size: 1em;
	margin: 0;
}
.castell h3 span {
	font-size: 0.7em;
}
